.container{
    min-width: 11rem;
    min-height: 10rem;
    position: relative;
    margin-top: 2rem;
    margin-bottom: 1rem;
    background: #E4E4E4;
    border-radius: 8px;
    box-shadow: rgba(0,0,0, 0.2) 0 3px 6px;
    padding: 1rem;
    margin-right: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    /* border: 2px solid red; */
    
}

.classDetails{
    width: 100%;
    display: flex;
    
}

.titleContainer{
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: -1rem;
}

.classTitle{
   font-size: 1.8rem;
   color: #AA33DC;
   font-family: 'Roboto Condensed', sans-serif;
   font-weight: 700;
   
}

.classDescription{
    color: #767676;
    font-size: 1rem;
    margin-top: -1rem;
    font-weight: 400;
}

.syllabiAmount{
    color: #767676;
    font-size: 1rem;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 300;
}

.syllabiList{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    margin-top: 0;
    height: fit-content;
    background: #F1DDAD;
}

.syllabiItem{

}


@media screen and (min-width: 1000px){
    .container{
        width: 15rem;
        height: 15rem;
    }
}