.homeContainer{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: 500ms;
    /* border: 2px solid red; */
}

.descriptionContainer{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 40rem;
    margin-top: 4rem;
}

.heading{
    color: #313131;
    font-size: 1.5rem;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 700;
}

.heading::after{
    content: '';
    position: absolute;
    width: 4px;
    height: 25px;
    background: #AA33DC;
    margin-left: 1rem;
    margin-top: 0.4rem;
}

.description{
    color: #767676;
    font-size: 1.2rem;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 400;
    margin-top: 0.2rem;
}

.details{
    width: 100%;
    color: #ADADAD;
    font-size: 1.1rem;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 400;
    margin-top: 0.2rem;
}

.githubButton{
    width: 10rem;
    height: 3rem;
    background: linear-gradient(to right, #DCAA33,#AA33DC 150%);
    border: none;
    outline: none;
    cursor: pointer;
    color: #EEEE;
    font-size: 1rem;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 400;
    border-radius: 4px;
    margin-top: 2rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    transition: 0.25s ease-out;
}

.githubButton:hover{
    background: #DCAA33;
    box-shadow: #0000006b 0 2px 8px;
    transform: translateY(-0.2rem);
}

@media screen and (min-width: 1000px){
    .descriptionContainer{
        width: 60%;
        margin-left: 14rem;
        /* justify-content: center; */
    }

    .heading{
        font-size: 2.8rem;
    }

    .description{
        font-size: 1.6rem;
        margin-top: 1rem;
    }

    .details{
        width: 60%;
        font-size: 1.4rem;
        margin-top: 1rem;
    }

    .heading::after{
        margin-left: 2rem;
        margin-top: 0.4rem;
        height: 40px;
    }

    .githubButton{
        width: 14rem;
        height: 4rem;
        font-size: 1.6rem;
    }
}
@media screen and (min-width: 1000px) and (max-width: 1200px) {
    .descriptionContainer{
        width: 80%;
        margin-left: 0rem;
        /* justify-content: center; */
    }

    .heading{
        font-size: 2.8rem;
    }

    .description{
        font-size: 1.6rem;
        margin-top: 1rem;
    }

    .details{
        width: 80%;
        font-size: 1.4rem;
        margin-top: 1rem;
    }

    .heading::after{
        margin-left: 2rem;
        margin-top: 0.4rem;
        height: 40px;
    }

    .githubButton{
        width: 14rem;
        height: 4rem;
        font-size: 1.6rem;
    }
}