.container{
    /* border: 2px solid red; */
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.logoContainer{
    
}

.logo{
    width: 4rem;
    height: 4rem;
}


.linksContainer{
    position: absolute;
    top: 0;
    right: 0;
    width: 60%;
    height: 100vh;
    display: none;
    flex-direction: column;
    align-items: center;
    transition: 1s;
    background: rebeccapurple;
    z-index: 1;
}

.linksContainer a{
    /* display: none; */
    text-decoration: none;
    font-size: 1.4rem;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 300;
    color: #ADADAD;
    margin-top: 2rem;
}

.menuButton{
    display: block;
}

.active{
    display: flex;
}

.menuOverlay{
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    background: rgba(0, 0, 0, 0.2);
    top: 0;
    left: 0;
}

@media screen and (min-width: 900px){
    .logo{
        width: 8rem;
        height: 8rem;
    }

    .container{
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: flex-start;
    }

    .menuOverlay{
        display: none;
    }

    .linksContainer{
        position: relative;
        width: 80%;
        height: 100%;
        margin-left: 2rem;
        display: flex;
        flex-direction: row;
        background: none;
    }
    .linksContainer a{
        font-size: 1.8rem;
        margin-left: 4rem;
    }

    .menuButton{
        display: none;
    }
}
